import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ModalHelper, _HttpClient } from '@delon/theme';
import { environment } from '@env/environment';
import { ConfigProjectAddComponent } from '../project-add/project-add.component';
import { ConfigProjectDetailComponent } from '../project-detail/project-detail.component';
import { ConfigProjectEditComponent } from '../project-edit/project-edit.component';

@Component({
  selector: 'app-config-project-list',
  templateUrl: './project-list.component.html',
  styleUrls: ['./project-list.component.less']
})
export class ConfigProjectListComponent implements OnInit {

  uncompleteOfSession: number = 0;
  allSession: number = 0;
  completeOfSession: number = 0;
  serchKey: string = '';
  scope: string = 'all';
  loading = false;
  data: Array<{
    projectId: number;
    projectName: string;
    amount: number;
    customerName: string,
    contractNumber: number,
    createTime: string,
    progress: number,
    ops: Array<{
      opTime: string,
      oper: {
        id: number,
        username: string,
        realname: string
      }
    }>,
    manager: {
      id: number,
      username: string,
      realname: string
    }
  }> = [];

  constructor(private http: _HttpClient, private modal: ModalHelper, private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {
    this.getData();
    this.getSessionNumber();
  }

  openAdd(): void {
    this.modal.create(ConfigProjectAddComponent, {}, { size: 1200 }).subscribe((res) => {
      this.cdr.detectChanges();
    });
  }

  openEdit(projectId: number): void {
    this.modal.create(ConfigProjectEditComponent, { projectId }, { size: 1200 }).subscribe((res) => {
      this.cdr.detectChanges();
    });
  }

  openDetail(projectId: number): void {
    this.modal.create(ConfigProjectDetailComponent, { projectId }, { size: 1200 }).subscribe((res) => {
      this.cdr.detectChanges();
    });
  }
  openApply(projectId: number) {
    this.http.put(environment.SERVER_URL + '/api/project/apply', projectId).subscribe(res => {
      this.getData();
    });
  }

  openDeploy(projectId: number) {
    this.modal.create(ConfigProjectDetailComponent, { projectId }, { size: 1200 }).subscribe((res) => {
      this.cdr.detectChanges();
    });
  }

  getData(): void {
    this.http.get(environment.SERVER_URL + '/api/project/getList', { pageNum: 0, pageSize: 5, key: this.serchKey, scope: this.scope }).subscribe((res) => {
      this.data = res.data;
      this.loading = false;
      this.cdr.detectChanges();
    });
  }

  /**
   * 
   */
  search() {
    this.getData();
  }

  /**
   * 
   * @param e 
   */
  searchChanged(e: any) {
    const keycode = window.event ? e.keyCode : e.which;
    if (keycode === 13) {// 
      this.search();
    }
    if (keycode === 27) {// esc
      this.serchKey = '';
    }
  }

  getSessionNumber() {
    this.http.get(environment.SERVER_URL + '/api/project/getSessionNumber').subscribe((res) => {
      if (res.data != null) {
        this.allSession = res.data.allSession;
        this.completeOfSession = res.data.completeOfSession;
        this.uncompleteOfSession = res.data.uncompleteOfSession;
      }
    });
  }

}
