import { ChangeDetectorRef } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { ModalHelper, _HttpClient } from '@delon/theme';
import { environment } from '@env/environment';
import { NzMessageService } from 'ng-zorro-antd/message';
import { ConfigDeploymentDetailComponent } from '../deployment-detail/deployment-detail.component';

@Component({
  selector: 'app-config-deployment-list',
  templateUrl: './deployment-list.component.html',
})
export class ConfigDeploymentListComponent implements OnInit {

  allOfSession: number = 0;
  completedOfSession: number = 0;
  completingOfSession: number = 0;
  uncompleteOfSession: number = 0;
  q = {
    q: '',
    status: 'all',
  };
  loading = false;
  data: Array<{
    projectId: number;
    projectName: string;
    amount: number;
    customerName: string,
    contractNumber: number,
    createTime: string,
    progress: number,
    ops: Array<{
      opTime: string,
      oper: {
        id: number,
        username: string,
        realname: string
      }
    }>,
    manager: {
      id: number,
      username: string,
      realname: string
    }
  }> = [];

  constructor(private http: _HttpClient, private modal: ModalHelper, private cdr: ChangeDetectorRef, private message: NzMessageService) { }

  ngOnInit(): void {
    this.getData();
    let role = localStorage.getItem("role")
    if (role == '50000') {
      this.getSessioNumber();
    }
  }

  openDetail(projectId: number): void {
    this.modal.create(ConfigDeploymentDetailComponent, { projectId }, { size: 1200 }).subscribe((res) => {
      this.cdr.detectChanges();
    });
  }

  getData(): void {
    this.loading = true;
    this.http.get(environment.SERVER_URL + '/api/deployment/getList', { pageNum: 0, pageSize: 5 }).subscribe((res) => {
      this.data = res.data;
      this.loading = false;
      this.cdr.detectChanges();
    });
  }

  getSessioNumber() {
    this.http.get(environment.SERVER_URL + '/api/deployment/sessionNumber').subscribe((res) => {
      this.allOfSession = res.data.allOfSession;
      this.completedOfSession = res.data.completedOfSession;
      this.completingOfSession = res.data.completingOfSession;
      this.uncompleteOfSession = res.data.uncompleteOfSession;
    });
  }

  /**
 *  
 */
  _submitForm(projectId: number): void {
    let role = localStorage.getItem("role")
    if (role != '50000') {
      this.message.create("error", "");
      return;
    }
    const params = {
      projectId: projectId
    };
    this.http.put(environment.SERVER_URL + '/api/deployment/edit?projectId=' + projectId).subscribe(res => {

    });
  }

}
