import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ModalHelper,_HttpClient } from '@delon/theme';
import { NzModalRef } from 'ng-zorro-antd/modal';
import { environment } from '@env/environment';
import { ConfigProductDetailComponent } from '../../product/product-detail/product-detail.component';

@Component({
  selector: 'app-config-deployment-detail',
  templateUrl: './deployment-detail.component.html',
})
export class ConfigDeploymentDetailComponent implements OnInit {
  projectId: number = -1;
  data: any = [];
  projectPachage: Array<number> = [];
  form!: FormGroup;

  constructor(private http: _HttpClient, private modal: NzModalRef,
    private fb: FormBuilder, private model: ModalHelper,
  ) {

  }

  ngOnInit(): void {
    this.form = this.fb.group({
      projectName: [null, [Validators.required]],
      contractAmount: [null, [Validators.required]],
      projectManager: [undefined, [Validators.required]],
      projectAdress: [null, [Validators.required]],
      customerName: [null, [Validators.required]],
      contractNumber: [null, [Validators.required]],
    });
    this.getData();
  }

  /**
   * 
   */
  getData() {
    this.http.get(environment.SERVER_URL + '/api/project/getProjectDetail', { projectId: this.projectId }).subscribe(res => {
      this.data = res.data;
    });
  }
  
  close(): void {
    this.modal.destroy();
  }

  /**
   * 
   */
  openProductDetail(productId: number){
    this.model.create(ConfigProductDetailComponent, { productId }, { size: 1200 }).subscribe((res) => {
      
    });
  }
}
