import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NzModalRef } from 'ng-zorro-antd/modal';
import { _HttpClient } from '@delon/theme';
import { NzMessageService } from 'ng-zorro-antd/message';
import { environment } from '@env/environment';

@Component({
  selector: 'app-config-product-edit',
  templateUrl: './product-edit.component.html',
})
export class ConfigProductEditComponent implements OnInit {

  // id
  productId: number = -1;
  productVersion: string = '';
  usersOfManager: Array<any> = [];
  usersOfTester: Array<any> = [];
  assets: Array<any> = [];
  detail: number = -1;
  form!: FormGroup;

  constructor(private fb: FormBuilder, private http: _HttpClient, private modal: NzModalRef, private message: NzMessageService) { }

  ngOnInit(): void {
    this.form = this.fb.group({
      productName: [{ value: null, disabled: true }, [Validators.required]],
      prodVersion: [{ value: null, disabled: true }, [Validators.required]],
      manager: [null, [Validators.required]],
      workContent: [null, [Validators.required]],
      prodAddress: [null, [Validators.required]],
      codeVersion: [null, [Validators.required]],
      tester: [null, [Validators.required]],
      message: [null, [Validators.required]],
    });
    this.getData();
    this.getAllUserOfTestManager();
    this.getAllUserOfProductManager();
  }

  getData() {
    this.http.get(environment.SERVER_URL + '/api/product/getProductDetails', { id: this.productId, productVersion: this.productVersion }).subscribe(res => {
      this.form.get("productName")?.setValue(res.data.productName);
      this.form.get("prodVersion")?.setValue(res.data.prodVersion);
      this.form.get("prodAddress")?.setValue(res.data.prodAddress);
      this.form.get("codeVersion")?.setValue(res.data.codeVersion);
      this.form.get("message")?.setValue(res.data.message);
    })
  }

  getAllUserOfProductManager() {
    this.http.get(environment.SERVER_URL + '/api/userManager/getAllUser', { role: 20000 }).subscribe((res) => {
      this.usersOfManager = res.data;
    });
  }

  getAllUserOfTestManager() {
    this.http.get(environment.SERVER_URL + '/api/userManager/getAllUser', { role: 60000 }).subscribe((res) => {
      this.usersOfTester = res.data;
    });
  }

  _submitForm(): void {
    let role = localStorage.getItem("role")
    if (role != '20000') {
      this.message.create("error", "");
      return;
    }
    const productMeta = {
      id: this.productId,
      description: this.form.get("message")?.value,
    };
    const product = {
      managerId: this.form.get("manager")?.value,
      productAddress: this.form.get("prodAddress")?.value,
      prodVersion: this.form.get("prodVersion")?.value,
      assetsList: this.assets,
      message: this.detail
    };
    const param = {
      productMeta: productMeta,
      product: product
    }
    this.http.put(environment.SERVER_URL + '/api/product/updateProduct', param).subscribe(res => {
      this.close();
    })
  }

  /**
   * 
   */
  customUpdateProductAssets = (item: any) => {
    this.http.get(environment.SERVER_URL + '/api/attached/OSS/getPolicy', { type: "productAssets" }).subscribe(res => {
      const { accessid, policy, signature, callback, dir, host } = res.data;
      let param = new FormData();
      param.append("OSSAccessKeyId", accessid);
      param.append("key", dir + item.file.name);
      param.append("policy", policy);
      param.append("Signature", signature);
      param.append("callback", callback);
      param.append("file", item.file as any);
      console.log(item);
      this.http.post(host, param)
        .subscribe(
          res => {
            this.assets.push({ id: res.data });
            item.onSuccess(item.file);
          },
          (err) => {
            item.onError(err, item.file);
          })
    });
  }

  /**
  * 
  */
  customUpdateProductDetail = (item: any) => {
    this.http.get(environment.SERVER_URL + '/api/attached/OSS/getPolicy', { type: "productDetail" }).subscribe(res => {
      const { accessid, policy, signature, callback, dir, host } = res.data;
      let param = new FormData();
      param.append("OSSAccessKeyId", accessid);
      param.append("key", dir + item.file.name);
      param.append("policy", policy);
      param.append("Signature", signature);
      param.append("callback", callback);
      param.append("file", item.file as any);
      this.http.post(host, param)
        .subscribe(
          (res) => {
            this.detail = res.data;
            item.onSuccess(item.file);
          },
          (err) => {
            item.onError(err, item.file);
          })
    });
  }


  close(): void {
    this.modal.destroy();
  }

}
