import { ChangeDetectorRef } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { ModalHelper, _HttpClient } from '@delon/theme';
import { environment } from '@env/environment';
import { ConfigVerifyDetailComponent } from '../verify-detail/verify-detail.component';

@Component({
  selector: 'app-config-verify-list',
  templateUrl: './verify-list.component.html',
})
export class ConfigVerifyListComponent implements OnInit {

  q = {
    q: '',
    status: 'all',
  };
  loading = false;
  data: Array<{
    projectId: number;
    projectName: string;
    amount: number;
    customerName: string,
    contractNumber: number,
    createTime: string,
    progress: number,
    ops: Array<{
      opTime: string,
      oper: {
        id: number,
        username: string,
        realname: string
      }
    }>,
    manager: {
      id: number,
      username: string,
      realname: string
    }
  }> = [];

  constructor(private http: _HttpClient, private modal: ModalHelper, private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {
    this.getData();
  }

  openDetail(projectId: number): void {
    this.modal.create(ConfigVerifyDetailComponent, { projectId }, { size: 1200 }).subscribe((res) => {
      this.cdr.detectChanges();
    });
  }

  getData(): void {
    this.http.get(environment.SERVER_URL + '/api/administer/getList', { pageNum: 0, pageSize: 5 }).subscribe((res) => {
      this.data = res.data;
      this.loading = false;
      this.cdr.detectChanges();
    });
  }


}
