import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { environment } from '@env/environment';
import { _HttpClient } from '@delon/theme';

import { NzModalRef } from 'ng-zorro-antd/modal';
import { Project } from '../../bean/Project';

@Component({
  selector: 'app-config-project-detail',
  templateUrl: './project-detail.component.html',
})
export class ConfigProjectDetailComponent implements OnInit {

  // id
  selectDeployerValue: any = null;
  // 
  allDeployers: any = null;
  projectId: number = -1;
  // 
  ProdsWithVersionOptions: any[] = [];
  // 
  afterSelectedProduct: Array<any> = [];
  deletedProduct: Array<any> = [];
  data!: Project;
  form!: FormGroup;

  constructor(private http: _HttpClient, private modal: NzModalRef,
    private fb: FormBuilder
  ) {

  }

  ngOnInit(): void {
    this.getProdsWithVersion();
    this.form = this.fb.group({
      projectName: [null, [Validators.required]],
      contractAmount: [null, [Validators.required]],
      projectManager: [undefined, [Validators.required]],
      projectAdress: [null, [Validators.required]],
      customerName: [null, [Validators.required]],
      contractNumber: [null, [Validators.required]],
    });
    this.getData();
  }

  getData() {
    this.http.get(environment.SERVER_URL + '/api/project/getProjectDetail', { projectId: this.projectId }).subscribe(res => {
      this.data = res.data;
    });
    // 
    this.http.get(environment.SERVER_URL + '/api/userManager/getUserByRole', { role: 50000 }).subscribe(res => {
      this.allDeployers = res.data;
      console.log(res);
    });
  }

  _submitForm(): void {
    Object.keys(this.form.controls).forEach((key) => {
      this.form.controls[key].markAsDirty();
      this.form.controls[key].updateValueAndValidity();
    });
    if (this.form.invalid) {
      return;
    }
  }

  /**
   * 
   */
  getProdsWithVersion() {
    this.http.get('/api/project/getProdsWithVersion').subscribe((res) => {
      this.ProdsWithVersionOptions = res.data;
    });
  }

  selectProd(values: any[]) {
    let item = {
      "productName": values[0].label,
      "productVersion": values[1].label,
      "productId": values[1].value
    };
    this.afterSelectedProduct.push(item);
  }

  /**
   *  afterSelectedProduct  productId 
   */
  removeAfterSelectedProduct(productId: number) {
    this.afterSelectedProduct = this.afterSelectedProduct.filter(
      item => {
        if (item.productId == productId) {
          return false;
        } else {
          return true;
        }
      }
    );
  }
  /**
   *  data.beforeSelectedProduct  productId 
   */
  removeBeforeSelectedProduct(productId: number) {
    this.data.beforeSelectedProduct = this.data.beforeSelectedProduct.filter(
      (item: any) => {
        if (item.productId == productId) {
          this.deletedProduct.push(item);
          return false;
        } else {
          return true;
        }
      }
    );
  }
  /**
   * 
   */
  close(): void {
    this.modal.destroy();
  }
  /**
   * 
   */
  deploy() {
    if (this.selectDeployerValue != null) {
      const body = {
        projectId: this.projectId,
        deployerId: this.selectDeployerValue
      }
      this.http.put(environment.SERVER_URL + '/api/project/deploy', body).subscribe(res => {
        console.log(res);
      })
    }
    this.close();
  }
}
