import { Component, OnInit } from '@angular/core';
import { ModalHelper, _HttpClient } from '@delon/theme';
import { environment } from '@env/environment';

@Component({
  selector: 'app-config-product-detail',
  templateUrl: './product-detail.component.html',
})
export class ConfigProductDetailComponent implements OnInit {

  productId: number = 1;
  data: any;
  constructor(private http: _HttpClient, private modal: ModalHelper) { }

  ngOnInit(): void {
    this.getData();
  }

  getData() {
    this.http.get(environment.SERVER_URL + '/api/product/getProductsDetails', { id: this.productId }).subscribe(res => {
      this.data = res.data;
    })
  }

  /**
   *  nz-card style
   */
  styleCardProductList(deprecated: boolean) {
    return deprecated == true ? { 'text-decoration': 'line-through', } : null;
  }
}
