import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ModalHelper, _HttpClient } from '@delon/theme';
import { NzModalRef } from 'ng-zorro-antd/modal';
import { NzMessageService } from 'ng-zorro-antd/message';
import { environment } from '@env/environment';
import { ConfigProductDetailComponent } from '../../product/product-detail/product-detail.component';

@Component({
  selector: 'app-config-verify-detail',
  templateUrl: './verify-detail.component.html',
})
export class ConfigVerifyDetailComponent implements OnInit {
  projectId: number = -1;
  data: any = [];
  projectPachage: Array<number> = [];
  form!: FormGroup;

  constructor(private http: _HttpClient, private modal: NzModalRef,
    private fb: FormBuilder, private model: ModalHelper, private message: NzMessageService
  ) {

  }

  ngOnInit(): void {
    this.form = this.fb.group({
      projectName: [null, [Validators.required]],
      contractAmount: [null, [Validators.required]],
      projectManager: [undefined, [Validators.required]],
      projectAdress: [null, [Validators.required]],
      customerName: [null, [Validators.required]],
      contractNumber: [null, [Validators.required]],
    });
    this.getData();
  }

  /**
   * 
   */
  getData() {
    this.http.get(environment.SERVER_URL + '/api/project/getProjectDetail', { projectId: this.projectId }).subscribe(res => {
      this.data = res.data;
    });
  }

  /**
   *  
   */
  _submitForm(): void {
    let role = localStorage.getItem("role")
    if (role != '40000') {
      this.message.create("error", "");
      return;
    }
    const param = {
      projectId: this.projectId,
      ids: this.projectPachage
    }
    this.http.put(environment.SERVER_URL + '/api/administer/edit', param).subscribe(res => {
      this.close();
    });
  }
  /**
   * 
   */
  customUpdateProjectPackage = (item: any) => {
    this.http.get(environment.SERVER_URL + '/api/attached/OSS/getPolicy', { type: "projectPackage" }).subscribe(res => {
      const { accessid, policy, signature, callback, dir, host } = res.data;
      let param = new FormData();
      param.append("OSSAccessKeyId", accessid);
      param.append("key", dir + item.file.name);
      param.append("policy", policy);
      param.append("Signature", signature);
      param.append("callback", callback);
      param.append("file", item.file as any);
      this.http.post(host, param)
        .subscribe(
          res => {
            this.projectPachage.push(res.data);
            item.onSuccess(item.file);
          },
          (err) => {
            item.onError(err, item.file);
          })
    });
  }
  close(): void {
    this.modal.destroy();
  }

  /**
   * 
   */
  openProductDetail(productId: number) {
    this.model.create(ConfigProductDetailComponent, { productId }, { size: 1200 }).subscribe((res) => {

    });
  }
}
