import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { FormGroup } from '@angular/forms';
import { Validators } from '@angular/forms';
import { _HttpClient } from '@delon/theme';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzModalRef } from 'ng-zorro-antd/modal';
import { environment } from '@env/environment';


@Component({
  selector: 'app-config-project-add',
  templateUrl: './project-add.component.html',
})
export class ConfigProjectAddComponent implements OnInit {
  // 
  prodsWithVersionOptions: any[] = [];
  usersOfManager: Array<any> = [];
  selectedProduct: Array<any> = [];
  form!: FormGroup;
  assets: Array<any> = [];

  constructor(private http: _HttpClient, private modal: NzModalRef,
    private fb: FormBuilder,
    private message: NzMessageService
  ) {

  }

  ngOnInit(): void {
    this.form = this.fb.group({
      productName: [null, [Validators.required]],
      prodVersion: [null, [Validators.required]],
      amount: [null, [Validators.required]],
      manager: [null, [Validators.required]],
      projectAddress: [null, [Validators.required]],
      projectCustomName: [null, [Validators.required]],
      projectNumber: [null, [Validators.required]],
    });
    this.getAllUserOfProjectManager();
    this.getAllProductVersion();
  }

  getAllUserOfProjectManager() {
    this.http.get(environment.SERVER_URL + '/api/userManager/getAllUser', { role: 30000 }).subscribe((res) => {
      this.usersOfManager = res.data;
    });
  }

  _submitForm(): void {
    let role = localStorage.getItem("role")
    if (role != '30000') {
      this.message.create("error", "");
      return;
    }
    let param = {
      projectName: this.form.get("productName")?.value,
      amount: this.form.get("amount")?.value,
      managerId: this.form.get("manager")?.value,
      projectAddress: this.form.get("projectAddress")?.value,
      projectCustomName: this.form.get("projectCustomName")?.value,
      projectNumber: this.form.get("projectNumber")?.value,
      progress: 1,
      productList: this.selectedProduct,
      assetsList: this.assets
    }
    this.http.post(environment.SERVER_URL + '/api/project/addProject', param).subscribe(res => {
      this.close();
    })
  }


  selectProd(values: any[]) {
    let item = {
      "productName": values[1].parent.productName,
      "prodVersion": values[1].productName,
      "productId": values[1].parent.productId
    };
    this.selectedProduct.push(item);
  }

  /**
   *  selectedProduct  productId 
   */
  removeSelectedProduct(productId: number) {
    this.selectedProduct = this.selectedProduct.filter(
      item => {
        if (item.productId == productId) {
          return false;
        } else {
          return true;
        }
      }
    );
  }

  close(): void {
    this.modal.destroy();
  }

  /**
   * 
   */
  getAllProductVersion() {
    this.http.get(environment.SERVER_URL + '/api/product/getAllProductVersion').subscribe(res => {
      this.prodsWithVersionOptions = res.data;
    })
  }


  /**
  * 
  */
  customUpdateProjectAssets = (item: any) => {
    this.http.get(environment.SERVER_URL + '/api/attached/OSS/getPolicy', { type: "projectAssets" }).subscribe(res => {
      const { accessid, policy, signature, callback, dir, host } = res.data;
      let param = new FormData();
      param.append("OSSAccessKeyId", accessid);
      param.append("key", dir + item.file.name);
      param.append("policy", policy);
      param.append("Signature", signature);
      param.append("callback", callback);
      param.append("file", item.file as any);
      this.http.post(host, param)
        .subscribe(
          res => {
            this.assets.push({ id: res.data });
            item.onSuccess(item.file);
          },
          (err) => {
            item.onError(err, item.file);
          })
    });
  }

}
