import { Component, OnInit } from '@angular/core';
import { ChangeDetectorRef } from '@angular/core';
import { NzMessageService } from 'ng-zorro-antd/message';
import { ModalHelper, _HttpClient } from '@delon/theme';
import { ConfigProductEditComponent } from '../product-edit/product-edit.component';
import { ConfigProductAddComponent } from '../product-add/product-add.component';
import { ConfigProductDetailComponent } from '../product-detail/product-detail.component';
import { environment } from '@env/environment';
import { ConfigProductUpdateComponent } from '../product-update/product-update.component';

@Component({
  selector: 'app-config-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.less']
})
export class ConfigProductListComponent implements OnInit {
  // 
  pageNum: number = 0;
  // 
  pageSize: number = 5;
  //
  total: number = 0;
  key = {
    key: '',
    status: 'all',
  };
  loading = false;
  data: Array<{
    productId: number;
    modifyTime: string;
    message: string;
    percent: string,
    productName: string,
    productVersion: string,
    manager: {
      id: number,
      username: string,
      realname: string
    }
  }> = [];

  constructor(private http: _HttpClient, private msg: NzMessageService, private modal: ModalHelper, private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {
    this.getData();
  }

  getData(): void {
    const params = {
      pageNum: this.pageNum,
      pageSize: 5,
      key: this.key.key
    };
    this.loading = true;
    this.http.get(environment.SERVER_URL + '/api/product/getList', params).subscribe((res) => {
      this.data = res.data;
      this.loading = false;
      this.cdr.detectChanges();
    });
    this.http.get(environment.SERVER_URL + '/api/product/productOfNumber').subscribe((res) => {
      this.total = res.data;
    });
  }

  /**
   * 
   */
  pageIndexChange(pageNum: number) {
    this.pageNum = pageNum;
    this.getData();
  }

  openEdit(productId: number, productVersion: string): void {
    this.modal.create(ConfigProductEditComponent, { productId: productId, productVersion: productVersion }, { size: 1200 }).subscribe((res) => {
      this.cdr.detectChanges();
    });
  }

  openAdd(): void {
    this.modal.create(ConfigProductAddComponent, { isAppend: false }, { size: 1200 }).subscribe((res) => {
      this.cdr.detectChanges();
    });
  }

  openDeatil(productId: number): void {
    this.modal.create(ConfigProductDetailComponent, { productId: productId }, { size: 1200 }).subscribe((res) => {
      this.cdr.detectChanges();
    });
  }

  openUpdate(productId: number, productName: string, description: string): void {
    this.modal.create(ConfigProductUpdateComponent, { isAppend: true, productId: productId, productName: productName, description: description }, { size: 1200 }).subscribe((res) => {
      this.cdr.detectChanges();
    });
  }

}
