import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { _HttpClient } from '@delon/theme';
import { Router } from '@angular/router';
import { environment } from '@env/environment';
import { NzModalRef } from 'ng-zorro-antd/modal';
import { NzMessageService } from 'ng-zorro-antd/message';

@Component({
  selector: 'app-config-project-edit',
  templateUrl: './project-edit.component.html',
})
export class ConfigProjectEditComponent implements OnInit {

  projectId: number = -1;
  // 
  prodsWithVersionOptions: any[] = [];
  // 
  afterSelectedProduct: Array<any> = [];
  // 
  deletedProduct: Array<any> = [];
  // 
  usersOfManager: Array<any> = [];
  // 
  beforeSelectedProduct: any[] = [];
  assetsList: Array<any> = [];
  data: any = [];
  form!: FormGroup;

  constructor(private http: _HttpClient,
    private fb: FormBuilder, private modal: NzModalRef,
    private message: NzMessageService,
    private router: Router,
  ) {

  }

  ngOnInit(): void {
    this.form = this.fb.group({
      projectName: [null, [Validators.required]],
      contractAmount: [null, [Validators.required]],
      projectManager: [undefined, [Validators.required]],
      projectAdress: [null, [Validators.required]],
      customerName: [null, [Validators.required]],
      contractNumber: [null, [Validators.required]],
    });
    this.getData();
    this.getAllUserOfProjectManager();
    this.getAllProductVersion();
  }

  getData() {
    this.http.get(environment.SERVER_URL + '/api/project/getProjectDetail', { projectId: this.projectId }).subscribe(res => {
      this.beforeSelectedProduct = res.data.beforeSelectedProduct;
      this.form.get("customerName")?.setValue(res.data.customerName);
      this.form.get("contractNumber")?.setValue(res.data.contractNumber);
      this.form.get("projectName")?.setValue(res.data.projectName);
      this.form.get("contractAmount")?.setValue(res.data.contractAmount);
      this.form.get("projectAdress")?.setValue(res.data.projectAddress);
    });
  }

  getAllUserOfProjectManager() {
    this.http.get(environment.SERVER_URL + '/api/userManager/getAllUser', { role: 30000 }).subscribe((res) => {
      this.usersOfManager = res.data;
    });
  }

  /**
 * 
 */
  getAllProductVersion() {
    this.http.get(environment.SERVER_URL + '/api/product/getAllProductVersion').subscribe(res => {
      this.prodsWithVersionOptions = res.data;
    })
  }

  _submitForm(): void {
    let role = localStorage.getItem("role")
    if (role != '30000') {
      this.message.create("error", "");
      return;
    }
    const param = {
      projectId: this.projectId,
      projectName: this.form.get("projectName")?.value,
      projectCustomName: this.form.get("customerName")?.value,
      amount: this.form.get("contractAmount")?.value,
      projectAddress: this.form.get("projectAdress")?.value,
      managerId: this.form.get("projectManager")?.value,
      projectNumber: this.form.get("contractNumber")?.value,
      appendProductList: this.afterSelectedProduct,
      deletedProductList: this.deletedProduct,
      assetsList: this.assetsList
    }
    this.http.put(environment.SERVER_URL + '/api/project/editProject', param).subscribe(res => {
      console.log(res);
    });
    this.close();
  }

  selectProd(values: any[]) {
    let item = {
      "productName": values[1].parent.productName,
      "prodVersion": values[1].productName,
      "productId": values[1].parent.productId
    };
    this.afterSelectedProduct.push(item);
  }

  /**
   *  afterSelectedProduct  productId 
   */
  removeAfterSelectedProduct(productId: number) {
    this.afterSelectedProduct = this.afterSelectedProduct.filter(
      item => {
        if (item.productId == productId) {
          return false;
        } else {
          return true;
        }
      }
    );
  }
  /**
   *  data.beforeSelectedProduct  productId 
   */
  removeBeforeSelectedProduct(productId: number) {
    this.beforeSelectedProduct = this.beforeSelectedProduct.filter(
      (item: any) => {
        if (item.productId == productId) {
          this.deletedProduct.push(item);
          return false;
        } else {
          return true;
        }
      }
    );
  }


  /**
  * 
  */
  customUpdateProjectDetail = (item: any) => {
    this.http.get(environment.SERVER_URL + '/api/attached/OSS/getPolicy', { type: "productDetail" }).subscribe(res => {
      const { accessid, policy, signature, callback, dir, host } = res.data;
      let param = new FormData();
      param.append("OSSAccessKeyId", accessid);
      param.append("key", dir + item.file.name);
      param.append("policy", policy);
      param.append("Signature", signature);
      param.append("callback", callback);
      param.append("file", item.file as any);
      this.http.post(host, param)
        .subscribe(
          (res) => {
            this.assetsList.push({ id: res.data });
            item.onSuccess(item.file);
          },
          (err) => {
            item.onError(err, item.file);
          })
    });
  }

  close(): void {
    this.modal.destroy();
  }
}
